import { Button, message, PageHeader, Radio, Space } from "antd";

import React, { useEffect, useState } from "react";

import { Row, Col } from "antd";
import { Link, useLocation, useNavigate } from "react-router-dom";
import MySteps from "./mySteps";
import { get, getDatabase, onValue, ref, set } from "firebase/database";
import { getUserid } from "../utils/MyFirebaseAuth";
import Ajv, { JTDSchemaType } from "ajv/dist/jtd"
import moment from "moment";

import styles from './button1.module.css';

interface group {
  color: string,
  names: string[]
}
interface WorkGroup {
  values: group[];
}
const schema: JTDSchemaType<WorkGroup> = {

  "properties": {
    values: { elements: { properties: { color: { "type": "string" }, names: { elements: { type: "string" } } } } },
  }
}

interface WorkItem {
  patientSerial: number,
  work: string,
  startTime: number,
  finishTime: number
}
interface WorkList {
  values: Record<string, WorkItem>
}
const schemaWorkList: JTDSchemaType<WorkList> = {

  "properties": {
    values: {
      values: {
        properties: {
          patientSerial: { type: "int32" },
          work: { type: "string" },
          finishTime: { type: "int32" },
          startTime: { type: "int32" }
        }
      }
    }
  }
}

const ajv = new Ajv()
const validateWorkGroup = ajv.compile(schema)
const parseWorkGroup = ajv.compileParser(schema)


const mybuttonScal = {
  minWidth: "100px",
  minHeight: "60px",
  fontSize: '200%'
};

type Prop = {
  nextpage: string;
  returnpage: string;
};

const readWorkGroup = new Promise<WorkGroup>((resolve, reject) => {
  /*
  const ssWorkgroup = sessionStorage.getItem("workgroup")
  if (ssWorkgroup) {
    const sWorkgroup = parseWorkGroup(ssWorkgroup)

    if (sWorkgroup) {
      console.log("load ssWorkgroup from sessionStorage.")


      return resolve(sWorkgroup);
    }
  }*/

  //sessionStorage.setItem("workgroupTimeout", ""+moment().unix()+1000);

  /*
  get(ref(getDatabase(), `users/${getUserid()}/workgroup`))
    .then((snapshot) => {
      const val = snapshot.val()
      if (!snapshot.exists()) {
        return reject("data not exist.");
      }
      if (!validateWorkGroup(val)) {
        return reject(validateWorkGroup.errors);
      }
      sessionStorage.setItem("workgroup", JSON.stringify(val))
      return resolve(val);
    })
    .catch((error) => {
      reject(error);
    });
  */

    onValue(ref(getDatabase(), `users/${getUserid()}/workgroup`), 
    (snapshot) => {
    
        const val = snapshot.val()
      if (!snapshot.exists()) {
        return reject("data not exist.");
      }
      if (!validateWorkGroup(val)) {
        return reject(validateWorkGroup.errors);
      }
      sessionStorage.setItem("workgroup", JSON.stringify(val))
      return resolve(val);

    }, {
      onlyOnce: true
    });
});

function addWorkItem(workItem: WorkItem) {
  return set(ref(getDatabase(), `users/${getUserid()}/worklist/${moment().unix()}`), {
    ...workItem
  })
}

export default function UserPage2(prop: Prop) {

  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [isDisabled, setIsDisabled] = useState<boolean>(true);
  const [workGroup, setWorkGroup] = useState<WorkGroup>();
  const [workItem, setWorkItem] = useState<WorkItem>();

  const search = useLocation().search;
  const gid = new URLSearchParams(search).get("id")
  const sId = gid ? Number(gid) : -1;




  useEffect(() => {
    setIsLoading(true)

    readWorkGroup.then(
      (value) => {
        console.log(value);
        setWorkGroup(value);
        setIsLoading(false);
      }).catch(error => {
        message.error(error)
        console.error(error)

      }
      )
  }, []);


  const postbutton = (work: string, color: string) => {
    color = color.replace("_", "#");
    return (
      <Col key={work}  >

        <button value={work}

          // style={{borderColor: color}}
          style={{ backgroundColor: color }}
          className={styles.button2}

          onClick={() => {
            speechSynthesis.cancel();
            let utterance = new SpeechSynthesisUtterance(work);
            utterance.lang = 'zh-TW';
            speechSynthesis.speak(utterance);
            setIsDisabled(false);
            setWorkItem({ work: work, patientSerial: sId, finishTime: -1, startTime: moment().unix() });
            console.log("onClick" + work);
          }}
        >
          {
            //work.length > 4 ? work : work.split("").join(" ")
            work

          }
        </button>

      </Col>
    );
  };


  return (
    <>
      <Row justify="center" align="top">
        <Col >
          <h1>就診序號: {sId}</h1>
        </Col>
      </Row>
      <Row justify="center" align="top">
        <Col >
          <h2>就診類型: <b> {workItem?.work}</b></h2>
        </Col>
      </Row>





      <Row justify="center" align="top" style={{ width: "100%", margin: "auto", textAlign: "center" }}>
        <Col style={{ width: "100%" }}>

          <Radio.Group defaultValue="" buttonStyle="solid" style={{ width: "100%", backgroundColor: "" }}>
            {
              workGroup?.values.map(group => {
                return (<Row align="middle" justify="center" key={group.color}>
                  {
                    group.names.map(name => {
                      return postbutton(name, group.color);
                    })
                  }
                </Row>
                )
              })

            }

          </Radio.Group>

          <br />
          <br></br>

          <Row justify="center" align="top" gutter={[24, 24]} >
            <Col>
              <Button
                style={mybuttonScal}
                loading={isLoading}
                disabled={isDisabled}
                type="primary"
                onClick={
                  () => {
                    if (workItem) {
                      setIsLoading(true)
                      addWorkItem(workItem).then(() => {
                        setIsLoading(false)
                        navigate(`/${prop.nextpage}?id=${sId}&work=${workItem.work}`)
                      });
                    }

                  }
                }
              >
                {"確認"}
              </Button>
            </Col>
            <Col>
              <Link to={"/" + prop.returnpage}>
                <Button loading={isLoading} style={mybuttonScal}>
                  {"取消"}
                </Button>
              </Link>
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
}
