import { Button, PageHeader, Space } from "antd";

import React, { useEffect, useState } from "react";

import { Row, Col } from "antd";
import { useLocation, useNavigate } from "react-router-dom";
import MySteps from "./mySteps";

const mybutton1 = {
  minWidth: "100px",
  minHeight: "60px",
  fontSize: '200%'
};


type Prop = {
  nextpage: string;

};

export default function UserPage2(prop: Prop) {
  const navigate = useNavigate();
  const search = useLocation().search;
  const sId = new URLSearchParams(search).get("id");
  const work = new URLSearchParams(search).get("work");

  const [mytimeout, setMytimeout] = useState(5);

  useEffect(() => {

    const timer1 = setInterval(() => {
      setMytimeout(mytimeout - 1)
      if (mytimeout === 0) {
        navigate(`/${prop.nextpage}`)
      }

    }, 1000);


    return () => clearInterval(timer1)
  }, [mytimeout])


  return (
    <>

      <Row justify="center" align="top">
        <Col >
          <h1>就診序號: {sId}</h1>
        </Col>
      </Row>
      <Row justify="center" align="top">
        <Col >
          <h2>就診類型: <b> {work}</b></h2>
        </Col>
      </Row>


      <Row justify="center" align="top">
        <Col >

          <h3>登記成功</h3>
          <Button type="primary" style={mybutton1} onClick={() => { navigate(`/${prop.nextpage}`) }} >確認</Button>
          <h4>{mytimeout} 秒後自動返回</h4>
          
        </Col>
      </Row>

    </>
  );
}
