
import React, { useState } from "react";
import { Button, Col, message, Row } from "antd";

import { useNavigate } from 'react-router-dom';
import MySteps from "./mySteps";

import styles from './button1.module.css'; 

const myborder1 = {
  width: "100px",
  height: "100px",
  fontSize: "48px"
};

const mybutton2 = {
  minWidth: "100px",
  minHeight: "60px",
  fontSize: '200%'
};

type Prop = {
  nextpage: string;
};

export default function UserPage1(prop: Prop) {
  const navigate = useNavigate()


  const [sId, setSId] = useState<string>("");

  const Square = (props: { value: string }) => {
    return (
      <button
        style={myborder1}
        
        onClick={() => {

          if (sId.length < 3) {
            // message.error("就診序號不能超過三位數，請重新輸入")
            setSId(sId + props.value)
          }
        }}
      >
        {props.value}
      </button>
    );
  };


  function detal() {
    const now = new Date()

    return (now.getHours() <= 12 ? "上午診" : "下午診") + " XXX 醫師"
  }

  return (
    <>
    
      <Row justify="center" align="top">
        <Col>
          <h1>就診序號: {sId}</h1>
        </Col>
      </Row>
      <Row justify="center" align="top">
        <Col >
          <h2>{detal()}</h2>
        </Col>
      </Row>

      <div style={{ height: "100%" }}>

        <Row align="top" justify="center">

              <Col key={1}>
                <Square value={"1"} />
              </Col>
              <Col key={2}>
                <Square value={"2"} />
              </Col>
              <Col key={3}>
                <Square value={"3"} />
              </Col>

        </Row>
        <Row align="top" justify="center">
          {['4', '5', '6'].map((value, index, array) => {
            return (
              <Col key={index}>
                <Square value={value} />
              </Col>
            );
          })}
        </Row>
        <Row align="top" justify="center">
          {['7', '8', '9'].map((value, index, array) => {
            return (
              <Col key={index}>
                <Square value={value} />
              </Col>
            );
          })}
        </Row>
        <Row align="top" justify="center">
          {['0'].map((value, index, array) => {
            return (
              <Col key={index}>
                <Square value={value} />
              </Col>
            );
          })}
        </Row>
      </div>
      <br />
      <Row align="top" justify="center" gutter={[24, 24]}>
        <Col key={"就診序號"}>

          <Button type="primary" style={mybutton2} onClick={() => {
            if (sId.length <= 0) {
              message.error("請輸入就診序號")
              return
            }
            if (sId.length > 3) {
              message.error("就診序號不能超過三位數，請重新輸入")
              return
            }



            navigate(`/${prop.nextpage}?id=${sId}`)
          }} >
            確認
          </Button>
        </Col>
        <Col key={"重新輸入"} >
          <Button style={mybutton2} type="dashed" onClick={() => setSId("")}>
            重新輸入
          </Button>
        </Col>
      </Row>
    </>
  );
}
